.touch-swiper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  overflow: hidden;
}
.touch-swiper-container {
  min-width: calc(100% * 3);
  width: calc(100% * 3);
  flex-basis: calc(100% * 3);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.touch-swiper-page {
  width: calc(100% / 3);
  height: 100%;
  flex-shrink: 0;
}
